<template>
  <div class="home">
    <el-container>
      <el-header>
        <img
          src="https://www.escook.cn/vue-shop/img/heima.b5a855ee.png"
          alt=""
        />
        <p></p>
        <el-button type="info" @click="tc"></el-button>
      </el-header>
      <el-container>
        <el-aside width="200">
          <el-radio-group v-model="isCollapse">
            <p @click="btn">|||</p>
          </el-radio-group>
          <el-menu
            :default-active="con"
            class="el-menu-vertical-demo"
            :collapse="isCollapse"
            background-color="#333744"
            text-color="#fff"
            active-text-color="rgb(64, 158, 255)"
            :unique-opened="flag"
          >
            <el-submenu
              v-for="(item, i) in list"
              :key="item.id"
              :index="item.path"
            >
              <template slot="title">
                <i :class="icon[i]"></i>
                <span slot="title">{{ item.authName }}</span>
              </template>
              <el-menu-item-group id="a">
                <el-menu-item
                  :index="items.path"
                  v-for="(items, ind) in item.children"
                  :key="items.id"
                  @click="path(item, items, i, ind)"
                >
                  <i class="el-icon-s-grid"></i>
                  {{ items.authName }}
                </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main>
          <el-breadcrumb separator-class="el-icon-arrow-right" v-if="str == ''">
            <el-breadcrumb-item>Welcome!!!</el-breadcrumb-item>
          </el-breadcrumb>
          <el-breadcrumb separator-class="el-icon-arrow-right" v-if="str != ''">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item v-if="str != ''">{{ str }}</el-breadcrumb-item>
            <el-breadcrumb-item v-if="strs != ''">
              {{ strs }}</el-breadcrumb-item
            >
          </el-breadcrumb>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "",
  // 
  components: {},
  // 
  props: {},
  // 
  data() {
    return {
      isCollapse: false,
      list: [],
      icon: [
        "el-icon-s-custom",
        "el-icon-box",
        "el-icon-s-goods",
        "el-icon-s-order",
        "el-icon-s-platform",
      ],
      con: window.localStorage.getItem("con") || "",
      flag: true,
      str: window.localStorage.getItem("str") || "",
      strs: window.localStorage.getItem("strs") || "",
    };
  },
  // Vue 
  created() {
   
  },
  // Vue  DOM
  mounted() {
    this.menu();
  },
  // vue
  update() {},
  // 
  methods: {
    btn() {
      this.isCollapse = !this.isCollapse;
    },
    async menu() {
      let { data } = await this.axios.get("/menus");

      this.list = data.data;
    },
    path(item, items, i, ind) {
      window.localStorage.setItem("str", item.authName);
      this.str = window.localStorage.getItem("str");
      window.localStorage.setItem("strs", items.authName);
      this.strs = window.localStorage.getItem("strs");

      window.localStorage.setItem("con", items.path);
      this.$router.push(`/users/${items.path}`);
      console.log(item, items, i, ind);
    },
    tc() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          window.sessionStorage.removeItem("token");
          window.localStorage.removeItem("con");
          this.con = "";
          window.localStorage.removeItem("str");
          this.str = "";

          window.localStorage.removeItem("strs");
          this.strs = "";
          this.$router.push("/");
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
  // 
  filter: {},
  // 
  computed: {},
  // 
  watch: {},
};
</script>

<style lang="scss" scoped >
</style>
