<template>
  <div class="sl">
    <el-main>
      <!--  -->
      <div class="head">
        <div>
          <el-input
            placeholder=""
            v-model="input3"
            class="input-with-select"
            :clearable="true"
            @clear="goods"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="goods"
            ></el-button>
          </el-input>
        </div>
        <el-button type="primary" @click="dialogFormVisible = true"
          ></el-button
        >
      </div>
      <el-table :data="tableData" stripe border style="width: 100%">
        <el-table-column prop="goods_id" label="#" width="50">
        </el-table-column>
        <el-table-column prop="goods_name" label="" width="600">
        </el-table-column>
        <el-table-column prop="goods_price" label="" width="100">
        </el-table-column>
        <el-table-column prop="goods_weight" label="" width="100">
        </el-table-column>
        <el-table-column label="" width="150">
          <template slot-scope="props">
            {{ props.row.add_time | lwh_fro("yyyy-M-d h:m:s") }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="props">
            <el-button size="mini" type="primary" @click="bj1(props.row)">
              <i class="el-icon-edit"></i>
            </el-button>
            <el-button size="mini" type="danger" @click="open(props.row)">
              <i class="el-icon-delete"></i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div class="foot">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[10, 15, 20]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-main>
  </div>
</template>

<script>
export default {
  name: "",
  // 
  components: {},
  // 
  props: {},
  // 
  data() {
    return {
      tableData: [],
      input3: "",
      pagenum: 1,
      pagesize: 10,
      total: 0,
      currentPage4: 1,
    };
  },
  // Vue 
  created() {},
  // Vue  DOM
  mounted() {
    this.goods();
  },
  // vue
  update() {},
  // 
  methods: {
    handleSizeChange(val) {
      this.pagesize = val;
      this.goods();
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.pagenum = val;
      this.goods();
      console.log(`: ${val}`);
    },
    async goods() {
      let { data } = await this.axios.get("goods", {
        params: {
          query: this.input3,
          pagenum: this.pagenum,
          pagesize: this.pagesize,
        },
      });
      
      this.tableData = data.data.goods;
      this.total = data.data.total;
    },
    // 
    async del(val) {
      let { data } = await this.axios.delete(`goods/${val.goods_id}`);
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);

      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.goods();
    },
    open(val) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.del(val);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
  // 
  filters: {
    lwh_fro(val, cont) {
      function dateFormat(date, format) {
        if (typeof date === "string") {
          var mts = date.match(/(\/Date\((\d+)\)\/)/);
          if (mts && mts.length >= 3) {
            date = parseInt(mts[2]);
          }
        }
        date = new Date(date);
        if (!date || date.toUTCString() == "Invalid Date") {
          return "";
        }
        var map = {
          M: date.getMonth() + 1, //
          d: date.getDate(), //
          h: date.getHours(), //
          m: date.getMinutes(), //
          s: date.getSeconds(), //
          q: Math.floor((date.getMonth() + 3) / 3), //
          S: date.getMilliseconds(), //
        };
        format = format.replace(/([yMdhmsqS])+/g, function (all, t) {
          var v = map[t];
          if (v !== undefined) {
            if (all.length > 1) {
              v = "0" + v;
              v = v.substr(v.length - 2);
            }
            return v;
          } else if (t === "y") {
            return (date.getFullYear() + "").substr(4 - all.length);
          }
          return all;
        });
        return format;
      }
      return dateFormat(val, cont);
    },
  },

  // 
  computed: {},
  // 
  watch: {},
};
</script>

<style lang="scss" scoped>
.sl {
  margin-top: 20px;
}
.el-main {
  background: #fff;
  border-radius: 10px;
  text-align: left;
}
.head {
  width: 100%;
  display: flex;
  align-items: center;
  > div {
    width: 368px;
  }
  > button {
    margin-left: 20px;
  }
  margin-bottom: 20px;
}
.el-table {
  font-size: 12px;
}
.foot {
  margin-top: 20px;
}
</style>
