<template>
  <div class="js">
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible1">
      <el-form
        :model="ruleForm1"
        :rules="rules1"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm1.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ms">
          <el-input v-model="ruleForm1.ms"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible1 = false"> </el-button>
        <el-button type="primary" @click="tj2()"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible2">
      <el-form
        :model="ruleForm2"
        :rules="rules2"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm2.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ms">
          <el-input v-model="ruleForm2.ms"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false"> </el-button>
        <el-button type="primary" @click="bj2()"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible3" @close="gb">
      <el-tree
        :data="tableDatas"
        :props="prop"
        show-checkbox
        ref="tree"
        node-key="id"
        default-expand-all
        :default-checked-keys="arr"
      >
      </el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible3 = false"> </el-button>
        <el-button type="primary" @click="qx2()"> </el-button>
      </div>
    </el-dialog>
    <el-main>
      <el-button
        type="primary"
        style="margin-bottom: 20px"
        @click="dialogFormVisible1 = true"
        ></el-button
      >

      <el-table :data="tableData" border :stripe="true" style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-row
              :class="['yrow', { 'srow-top': index == 0 }, 'srow-bot']"
              :gutter="20"
              v-for="(item, index) in props.row.children"
              :key="index"
            >
              <el-col :span="6">
                <div class="grid-content bg-purple">
                  <el-tag
                    effect="dark"
                    closable
                    @close="close(props.row.id, item.id)"
                  >
                    {{ item.authName }}
                  </el-tag>
                  <i class="el-icon-caret-right"></i>
                </div>
              </el-col>
              <el-col :span="6" style="padding: 0px">
                <div class="grid-content bg-purple" width="100%">
                  <el-row
                    style="margin: 0px"
                    :class="['srow', { 'srow-top': indexs > 0 }]"
                    :gutter="20"
                    v-for="(items, indexs) in item.children"
                    :key="indexs"
                  >
                    <el-col :span="6"
                      ><div class="grid-content bg-purple">
                        <el-tag
                          type="success"
                          effect="dark"
                          closable
                          @close="close(props.row.id, items.id)"
                        >
                          {{ items.authName }}
                        </el-tag>
                        <i class="el-icon-caret-right"></i></div
                    ></el-col>
                    <el-col
                      ><div class="grid-content bg-purple">
                        <div
                          v-for="(itemx, indexx) in items.children"
                          :key="indexx"
                        >
                          <el-tag
                            class="ei"
                            type="warning"
                            effect="dark"
                            closable
                            @close="close(props.row.id, itemx.id)"
                          >
                            {{ itemx.authName }}
                          </el-tag>
                        </div>
                      </div></el-col
                    >
                  </el-row>
                </div>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="#" prop="id"> </el-table-column>
        <el-table-column label="" prop="roleName"> </el-table-column>
        <el-table-column label="" prop="roleDesc"> </el-table-column>
        <el-table-column label="">
          <template slot-scope="prop">
            <el-button size="mini" type="primary" @click="bj1(prop.row)">
              <i class="el-icon-edit"></i>
            </el-button>
            <el-button size="mini" type="danger" @click="del(prop.row)">
              <i class="el-icon-delete"></i>
            </el-button>
            <el-button size="mini" type="warning" @click="qx(prop.row)">
              <i class="el-icon-s-tools"></i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </div>
</template>

<script>
export default {
  name: "",
  // 
  components: {},
  // 
  props: {},
  // 
  data() {
    return {
      tableData: [],
      tableDatas: [],
      dialogFormVisible1: false,
      ruleForm1: {
        name: "",
        ms: "",
      },
      rules1: {
        name: [{ required: true, message: "", trigger: "blur" }],
        ms: [{ required: true, message: "", trigger: "blur" }],
      },
      dialogFormVisible2: false,
      ruleForm2: {
        name: "",
        ms: "",
        id: "",
      },
      rules2: {
        name: [{ required: true, message: "", trigger: "blur" }],
        ms: [{ required: true, message: "", trigger: "blur" }],
      },
      dialogFormVisible3: false,
      prop: {
        label: "authName",
        children: "children",
      },
      arr: [],
      qxid: null,
    };
  },
  // Vue 
  created() {},
  // Vue  DOM
  mounted() {
    this.roles();
  },
  // vue
  update() {},
  // 
  methods: {
    async roles() {
      //  this.axios.get("roles")
      //  .then(res => {
      //    console.log(res)
      //  })
      //  .catch(err => {
      //    console.error(err);
      //  })
      let { data } = await this.axios.get("roles");

      this.tableData = data.data;
    },
    async tj2() {
      let { data } = await this.axios.post("roles", {
        roleName: this.ruleForm1.name,
        roleDesc: this.ruleForm1.ms,
      });
      console.log(data);
      if (data.meta.status != 201) return this.$message.error(data.meta.msg);
      console.log(data);
      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.dialogFormVisible1 = false;
      this.roles();
    },
    bj1(val) {
      this.ruleForm2.name = val.roleName;
      this.ruleForm2.ms = val.roleDesc;
      this.ruleForm2.id = val.id;
      this.dialogFormVisible2 = true;
      console.log(this.ruleForm2);
    },
    async bj2() {
      console.log(this.ruleForm2);
      let { data } = await this.axios.put(`roles/${this.ruleForm2.id}`, {
        roleName: this.ruleForm2.name,
        roleDesc: this.ruleForm2.ms,
      });

      console.log(data);
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);
      console.log(data);
      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.dialogFormVisible2 = false;
      this.roles();
    },
    async del2(val) {
      let { data } = await this.axios.delete(`roles/${val.id}`);

      if (data.meta.status != 200) return this.$message.error(data.meta.msg);
      console.log(data);
      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.roles();
    },
    del(val) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.del2(val);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    // 
    // 
    // async rights() {
    //   let { data } = await this.axios.get("rights/tree ");
    //   this.tableDatas = data.data;
    //   console.log(data);
    // },
    async qx(val) {
      let { data } = await this.axios.get("rights/tree ");
      this.tableDatas = data.data;
      this.qxid = val.id;
      this.dg(val, this.arr);
      console.log(this.arr);
      this.dialogFormVisible3 = true;
    },

    // 
    async close(jid, qid) {
      let { data } = await this.axios.delete(`roles/${jid}/rights/${qid}`);
      console.log(data);
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);
      console.log(data);
      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.roles();
    },
    // 3id
    dg(node, arr) {
      if (!node.children) {
        return arr.push(node.id);
      }
      node.children.forEach((item) => {
        this.dg(item, arr);
      });
    },
    gb() {
      this.arr = [];
    },
    async qx2() {
      let key = [
        ...this.$refs.tree.getCheckedKeys(),
        ...this.$refs.tree.getHalfCheckedKeys(),
      ];
      let keys = key.join(",");
      let { data } = await this.axios.post(`roles/${this.qxid}/rights`, {
        rids: keys,
      });
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);
      console.log(data);
      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.roles();
      this.dialogFormVisible3 =false;

    },
  },

  // 
  filter: {},
  // 
  computed: {},
  // 
  watch: {},
};
</script>

<style lang="scss" scoped>
.js {
  margin-top: 20px;
}
.el-main {
  background: #fff;
  border-radius: 10px;
  text-align: left;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.el-table td > div {
  box-sizing: border-box;
}

.el-row {
  border-left: 1px solid #eee;
}
.yrow {
  border-right: 1px solid #eee;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  > .el-col:nth-of-type(1) {
    display: flex;
    justify-content: center;
  }
  > .el-col:nth-of-type(2) {
    flex: 1;
  }
}
.srow {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  margin: 0px;

  > .el-col:nth-of-type(1) {
    display: flex;
    justify-content: center;
  }
  > .el-col:nth-of-type(2) {
    flex: 1;
  }
}
.srow-top {
  border-top: 1px solid #eee;
}
.srow-bot {
  border-bottom: 1px solid #eee;
}
.bg-purple {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.ei {
  margin: 10px;
}
</style>
