<template>
  <div class="fl">
    <!--  -->
    <el-dialog :title="tit" :visible.sync="dialogFormVisible1">
      <el-form
        :model="ruleForm1"
        :rules="rules1"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item :label="name" prop="name" hide-required-asterisk>
          <el-input v-model="ruleForm1.name"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible1 = false"> </el-button>
        <el-button type="primary" @click="addButes(tab)"> </el-button>
      </div>
    </el-dialog>
    
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible2">
      <el-form
        :model="ruleForm1"
        :rules="rules1"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name" hide-required-asterisk>
          <el-input v-model="ruleForm2.name"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false"> </el-button>
        <el-button type="primary" @click="putAxios"> </el-button>
      </div>
    </el-dialog>
    <el-main>
      <el-alert
        title=""
        :closable="false"
        type="warning"
        show-icon
        effect="dark"
      >
      </el-alert>
      <div style="margin-top: 20px; margin-bottom: 20px">
        <span class="demonstration" style="margin-right: 10px"
          >:</span
        >
        <el-cascader
          v-model="value"
          :options="tableData"
          :props="{ value: 'cat_id', label: 'cat_name' }"
          @change="handleChange"
        ></el-cascader>
      </div>
      <el-tabs type="border-card" @tab-click="hq()" v-model="tab">
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            style="margin-top: 20px; margin-bottom: 20px"
            size="small"
            @click="dialogFormVisible1 = true"
            :disabled="value == ''"
            ></el-button
          >
          <el-table :data="tableDats" border style="width: 100%">
            <el-table-column width="40" type="expand">
              <template slot-scope="prop">
                <el-tag
                  v-show="prop.row.attr_vals != ''"
                  v-for="tag in prop.row.attr_vals.split(',')"
                  :key="tag"
                  closable
                  @close="handleClose(tag)"
                  style="margin-right: 10px"
                >
                  {{ tag }}
                </el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="inputVisible"
                  v-model="inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(prop.row.attr_vals)"
                  @blur="handleInputConfirm(prop.row.attr_vals)"
                ></el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index" label="#" width="40">
            </el-table-column>
            <el-table-column prop="attr_name" label="">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="prop">
                <el-button
                  type="primary"
                  size="small"
                  icon="el-icon-edit"
                  @click="putThis(prop.row)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  size="small"
                  icon="el-icon-delete"
                  @click="open(prop.row)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button
            size="small"
            type="primary"
            style="margin-top: 20px; margin-bottom: 20px"
            @click="dialogFormVisible1=true"
            ></el-button
          >
          <el-table :data="tableDats" border style="width: 100%">
            <el-table-column width="40" type="expand">
              <template slot-scope="prop">
                <el-tag
                  v-show="prop.row.attr_vals != ''"
                  v-for="tag in prop.row.attr_vals.split(',')"
                  :key="tag"
                  closable
                  @close="handleClose(tag)"
                  style="margin-right: 10px"
                >
                  {{ tag }}
                </el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="inputVisible"
                  v-model="inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm"
                  @blur="handleInputConfirm"
                ></el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index" label="#" width="40">
            </el-table-column>
            <el-table-column prop="attr_name" label="">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="prop">
                <el-button
                  type="primary"
                  size="small"
                  icon="el-icon-edit"
                  @click="putThis(prop.row)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  size="small"
                  icon="el-icon-delete"
                  @click="open(prop.row)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-main>
  </div>
</template>

<script>
export default {
  name: "",
  // 
  components: {},
  // 
  props: {},
  // 
  data() {
    return {
      tableData: [],
      tableDats: [],
      value: [],
      tab: "many",
      inputValue: "",
      inputVisible: false,
      dialogFormVisible1: false,
      tit:"",
      name:"",
      ruleForm1: {
        name: "",
      },
      rules1: {
        name: [{ required: true, message: "", trigger: "blur" }],
      },
      dialogFormVisible2: false,
      ruleForm2: {
        name: "",
        id: 0,
      },
      rules2: {
        name: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  // Vue 
  created() {},
  // Vue  DOM
  mounted() {
    this.categories();
  },
  // vue
  update() {},
  // 
  methods: {
    async categories() {
      let { data } = await this.axios.get("categories");

      this.tableData = data.data;
    },
    async addAxiosCate(id, sel) {
      let { data } = await this.axios.get(`categories/${id}/attributes`, {
        params: {
          sel: sel,
        },
      });
      console.log(data.data);
      this.tableDats = data.data;
    },
    async hq() {
      this.addAxiosCate(this.value[this.value.length - 1], this.tab);
    },
    handleChange() {
      this.addAxiosCate(this.value[this.value.length - 1], this.tab);
      console.log(1);
    },
    // tag input
    handleInputConfirm(val) {
      console.log(val)
      this.inputVisible = false;
      this.inputValue = "";
    },
    // tag 
    handleClose(tag) {
      console.log(tag);
    },
    // tag 
    showInput() {
      this.inputVisible = true;
      // this.$nextTick(() => {
      //   this.$refs.saveTagInput.$refs.input.focus();
      // });
    },
    // 
    async addButes(val) {
      console.log(val)
      let { data } = await this.axios.post(
        `categories/${this.value[this.value.length - 1]}/attributes`,
        {
          attr_name: this.ruleForm1.name,
          attr_sel: val,
          attr_vals: null,
        }
      );
      console.log(data.data);
      if (data.meta.status != 201) return this.$message.error(data.meta.msg);

      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.addAxiosCate(this.value[this.value.length - 1], this.tab);
      this.ruleForm1.name=""
      this.dialogFormVisible1 = false;
    },
    // 
    async delAxios(val) {
      let { data } = await this.axios.delete(
        `categories/${this.value[this.value.length - 1]}/attributes/${val}`
      );
      console.log(data.data);
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);

      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.addAxiosCate(this.value[this.value.length - 1], this.tab);
    },
    open(val) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.delAxios(val.attr_id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    async putAxios() {
      let { data } = await this.axios.put(
        `categories/${this.value[this.value.length - 1]}/attributes/${
          this.ruleForm2.id
        }`,
        {
          attr_name: this.ruleForm2.name,
          attr_sel: this.tab,
        }
      );
      console.log(data.data);
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);

      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.addAxiosCate(this.value[this.value.length - 1], this.tab);
      this.dialogFormVisible2 = false;
    },
    putThis(val) {
      this.ruleForm2.name = val.attr_name;
      this.ruleForm2.id = val.attr_id;
      this.dialogFormVisible2 = true;
    },
  },
  // 
  filter: {},
  // 
  computed: {},
  // 
  watch: {
    tab(val){
      if(val == "only"){
        this.tit = ""
        this.name=""
      }else{
        this.tit = ""
        this.name=""
      }
    }
  },
};
</script>

<style lang="scss" scoped>
.fl {
  margin-top: 20px;
}
.el-main {
  background: #fff;
  border-radius: 10px;
  text-align: left;
}
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>
