<template>
  <div class="sf">
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible1">
      <el-form
        :model="ruleForm1"
        :rules="rules1"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
        
      >
        <el-form-item label="" prop="name" hide-required-asterisk>
          <el-input v-model="ruleForm1.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-cascader
            v-model="ruleForm1.fj"
            width="100%"
            :options="a"
            :props="{
              value: 'cat_id',
              label: 'cat_name',
              checkStrictly: true,
              children: 'children',
            }"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible1 = false"> </el-button>
        <el-button type="primary" @click="fl2()"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible2">
      <el-form
        :model="ruleForm2"
        :rules="rules2"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name" hide-required-asterisk>
          <el-input v-model="ruleForm2.name"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false"> </el-button>
        <el-button type="primary" @click="bj2()"> </el-button>
      </div>
    </el-dialog>
    <el-main>
      <!--  -->
      <div class="head">
        <el-button type="primary" @click="dialogFormVisible1 = true"
          ></el-button
        >
      </div>
      <el-table
        :data="tableData"
        stripe
        border
        row-key="cat_id"
        style="width: 100%"
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
      >
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column label="" prop="cat_name"> </el-table-column>
        <el-table-column label="">
          <template slot-scope="props">
            <i
              v-show="props.row.cat_deleted"
              class="el-icon-success"
              style="color: #39ce39"
            ></i>
            <i
              v-show="!props.row.cat_deleted"
              class="el-icon-error"
              style="color: red"
            ></i>
          </template>
        </el-table-column>
        <el-table-column prop="cat_level" label="">
          <template slot-scope="props">
            <el-tag v-show="props.row.cat_level == 0" effect="dark"
              ></el-tag
            >
            <el-tag
              v-show="props.row.cat_level == 1"
              effect="dark"
              type="success"
              ></el-tag
            >

            <el-tag
              v-show="props.row.cat_level == 2"
              effect="dark"
              type="warning"
              ></el-tag
            >
          </template>
        </el-table-column>

        <el-table-column label="">
          <template slot-scope="props">
            <el-button size="mini" type="primary" @click="bj1(props.row)">
              <i class="el-icon-edit"></i>
            </el-button>
            <el-button size="mini" type="danger" @click="open(props.row)">
              <i class="el-icon-delete"></i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div class="foot">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-main>
  </div>
</template>

<script>
export default {
  name: "",
  // 
  components: {},
  // 
  props: {},
  // 
  data() {
    return {
      tableData: [],
      input3: "",
      pagenum: 1,
      pagesize: 5,
      total: 0,
      currentPage4: 1,
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      ruleForm1: {
        name: "",
        fj: "",
      },
      rules1: {
        name: [{ required: true, message: "", trigger: "blur" }],
      },
      ruleForm2: {
        name: "",
        id: 0,
      },
      rules2: {
        name: [{ required: true, message: "", trigger: "blur" }],
      },
      a: [],
    };
  },
  // Vue 
  created() {},
  // Vue  DOM
  mounted() {
    this.categories();
    this.categorie();
  },
  // vue
  update() {},
  // 
  methods: {
    handleSizeChange(val) {
      this.pagesize = val;
      this.categories();
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.pagenum = val;
      this.categories();
      console.log(`: ${val}`);
    },
    async categories() {
      let { data } = await this.axios.get("categories", {
        params: {
          pagenum: this.pagenum,
          pagesize: this.pagesize,
        },
      });
      console.log(data);
      this.tableData = data.data.result;
      this.total = data.data.total;
    },
    async categorie() {
      let { data } = await this.axios.get("categories", {
        params: {
          pagenum: this.pagenum,
          pagesize: this.pagesize,
        },
      });

      data.data.result.map((i) => {
        if (i.children) {
          i.children.map((is) => {
            delete is.children;
          });
        }
      });
      this.a = data.data.result;
    },

    async fl2() {
      let { data } = await this.axios.post("categories", {
        cat_pid: this.ruleForm1.fj[this.ruleForm1.fj.length - 1],
        cat_name: this.ruleForm1.name,
        cat_level: this.ruleForm1.fj.length,
      });
      if (data.meta.status != 201) return this.$message.error(data.meta.msg);

      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.categories();
      this.dialogFormVisible2=false
    },
    // 
    bj1(val) {
      this.ruleForm2.name = val.cat_name;
      this.ruleForm2.id = val.cat_id;
      this.dialogFormVisible2 = true;
    },
    async bj2() {
      console.log(this.ruleForm2);
      let { data } = await this.axios.put(`categories/${this.ruleForm2.id}`, {
        cat_name: this.ruleForm2.name,
      });
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);

      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.categories();
    },
    // 
    async del(val) {
      let { data } = await this.axios.delete(`categories/${val.cat_id}`);
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);

      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.categories();
    },
    open(val) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.del(val);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },

  // 
  filter: {},
  // 
  computed: {},
  // 
  watch: {},
};
</script>

<style lang="scss" scoped>
.sf {
  margin-top: 20px;
}
.el-main {
  background: #fff;
  border-radius: 10px;
  text-align: left;
}
.head {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.el-table {
  font-size: 12px;
}
.foot {
  margin-top: 20px;
}
.el-cascader {
  width: 100%;
}
</style>
