<template>
  <div class="list">
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mm">
          <el-input v-model="ruleForm.mm"></el-input>
        </el-form-item>
        <el-form-item label="" prop="dh">
          <el-input v-model="ruleForm.dh"></el-input>
        </el-form-item>
        <el-form-item label="" prop="yx">
          <el-input v-model="ruleForm.yx"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="tj()"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible2">
      <el-form
        :model="ruleForm2"
        :rules="rules2"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input :disabled="true" v-model="ruleForm2.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="dh">
          <el-input v-model="ruleForm2.dh"></el-input>
        </el-form-item>
        <el-form-item label="" prop="yx">
          <el-input v-model="ruleForm2.yx"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false"> </el-button>
        <el-button type="primary" @click="bj2()"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible3">
      <el-form
        :model="ruleForm3"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          {{ ruleForm3.name }}
        </el-form-item>
        <el-form-item label="" prop="js">
          {{ ruleForm3.js }}
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="ruleForm3.region" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible3 = false"> </el-button>
        <el-button type="primary" @click="js2()"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-container>
      <el-main>
        <div class="head">
          <div>
            <el-input
              placeholder=""
              v-model="input3"
              class="input-with-select"
              :clearable="true"
              @clear="user"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="user"
              ></el-button>
            </el-input>
          </div>
          <el-button type="primary" @click="dialogFormVisible = true"
            ></el-button
          >
        </div>
        <div class="body">
          <table>
            <thead>
              <tr>
                <td v-for="(item, index) in list_tit" :key="index">
                  {{ item }}
                </td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in tableData" :key="index">
                <td>
                  {{ item.id }}
                </td>
                <td>
                  {{ item.username }}
                </td>
                <td>
                  {{ item.email }}
                </td>
                <td>
                  {{ item.mobile }}
                </td>
                <td>
                  {{ item.role_name }}
                </td>
                <td>
                  <el-switch
                    @change="zt(item)"
                    v-model="item.mg_state"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                  >
                  </el-switch>
                </td>

                <td>
                  <el-button size="mini" type="primary" @click="bj1(item)">
                    <i class="el-icon-edit"></i>
                  </el-button>
                  <el-button size="mini" type="danger" @click="open(item)">
                    <i class="el-icon-delete"></i>
                  </el-button>
                  <el-button size="mini" type="warning" @click="js1(item)">
                    <i class="el-icon-s-tools"></i>
                  </el-button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="foot">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage4"
            :page-sizes="[3, 6, 9]"
            :page-size="100"
            layout="total, sizes, prev, pager, next, jumper"
            :total="zs"
          >
          </el-pagination>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "",
  // 
  components: {},
  // 
  props: {},
  // 
  data() {
    return {
      input3: "",
      tableData: [],
      currentPage4: 1,
      pagenum: 1,
      pagesize: 3,
      
      list_tit: ["#", "", "", "", "", "", ""],
      zs: 0,
      dialogFormVisible: false,
      dialogFormVisible2: false,
      dialogFormVisible3: false,
      ruleForm: {
        name: "",
        dh: "",
        yx: "",
        mm: "",
      },
      ruleForm2: {
        name: "",

        dh: "",
        yx: "",
        id: "",
      },
      ruleForm3: {
        name: "",
        js: "",
        id: "",
        region: "",
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        mm: [{ required: true, message: "", trigger: "blur" }],
        dh: [{ required: true, message: "", trigger: "blur" }],
        yx: [{ required: true, message: "", trigger: "blur" }],
      },
      rules2: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        dh: [{ required: true, message: "", trigger: "blur" }],
        yx: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  // Vue 
  created() {},
  // Vue  DOM
  mounted() {
    this.user();
  },
  // vue
  update() {},
  // 
  methods: {
    handleSizeChange(val) {
      this.pagesize = val;
      this.user();
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.pagenum = val;
      this.user();
      console.log(`: ${val}`);
    },
    async user() {
      let { data } = await this.axios.get("users", {
        params: {
          query:this.input3,
          pagenum: this.pagenum,
          pagesize: this.pagesize,
        },
      });

      this.zs = data.data.total;
      this.tableData = data.data.users;
    },
    async tj() {
      let { data } = await this.axios.post("users", {
        username: this.ruleForm.name,
        password: this.ruleForm.mm,
        mobile: this.ruleForm.yx,
        email: this.ruleForm.dh,
      });
      if (data.meta.status != 201) return this.$message.error("");

      this.$message({
        message: "",
        type: "success",
      });
      this.user();
      this.dialogFormVisible = false;
    },
    async zt(val) {
      console.log(val.id);
      let { data } = await this.axios.put(
        `users/${val.id}/state/${val.mg_state}`
      );
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);
      this.$message({
        message: "",
        type: "success",
      });
      this.user();
    },
    // async cx() {
    //   let { data } = await this.axios.get(`users/${this.input3}`);
    //   if (data.meta.status != 200) return this.$message.error(data.meta.msg);
    //   console.log(data);
    //   this.$message({
    //     message: data.meta.msg,
    //     type: "success",
    //   });
    //   this.tableData = [];
    //   this.tableData.push(data.data);

    //   this.zs = this.tableData.length;
    // },
    bj1(val) {
      this.ruleForm2.name = val.username;
      this.ruleForm2.dh = val.mobile;
      this.ruleForm2.yx = val.email;
      this.ruleForm2.id = val.id;
      console.log(val);
      this.dialogFormVisible2 = true;
    },
    async bj2() {
      console.log(1);
      let { data } = await this.axios.put(`users/${this.ruleForm2.id}`, {
        email: this.ruleForm2.yx,
        mobile: this.ruleForm2.dh,
      });
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);
      console.log(data);
      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.dialogFormVisible2 = false;
      this.user();
    },
    async del(val) {
      let { data } = await this.axios.delete(`users/${val.id}`);
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);
      this.$message({
        type: "success",
        message: "!",
      });
      this.user();
    },
    async open(val) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.del(val);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    js1(val) {
      this.ruleForm3.name = val.username;
      this.ruleForm3.js = val.role_name;
      this.ruleForm3.id = val.id;
      console.log(val);
      this.dialogFormVisible3 = true;
    },
     async js2() {
      console.log(1);
      let { data } = await this.axios.put(`users/${this.ruleForm3.id}/role`, {
        rid:this.ruleForm3.js
      });
      console.log(data)
      if (data.meta.status != 200) return this.$message.error(data.meta.msg);
      console.log(data);
      this.$message({
        message: data.meta.msg,
        type: "success",
      });
      this.dialogFormVisible3 = false;
      this.user();
    },
  },
  // 
  filter: {},
  // 
  computed: {},
  // 
  watch: {},
};
</script>

<style lang="scss" scoped>
.list {
  margin-top: 20px;
}
.el-main {
  background-color: #fff;
  border-radius: 10px;
}
.head {
  width: 100%;
  display: flex;
  align-items: center;
  > div {
    width: 368px;
  }
  > button {
    margin-left: 20px;
  }
  margin-bottom: 20px;
}
.foot {
  margin-top: 20px;
  display: flex;
  justify-content: flex-start;
}
table {
  width: 100%;
  border-collapse: collapse;
  td {
    border: 1px solid #eee;
    height: 40px;
  }
}

</style>
