<template>
  <div class="dd">
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible1">
      <el-timeline reverse="reverse">
        <el-timeline-item
          v-for="(activity, index) in activities"
          :key="index"
          :timestamp="activity.time"
          :icon="activity.icon"
          :color="activity.color"
        >
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible1 = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisible1 = false"
          > </el-button
        >
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible2">
      <el-form
        :model="ruleForm1"
        :rules="rules1"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="/" prop="fj" hide-required-asterisk>
          <el-cascader :options="cityOptions"> </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="name" hide-required-asterisk>
          <el-input v-model="ruleForm1.name"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false"> </el-button>
        <el-button type="primary" @click="xg2"> </el-button>
      </div>
    </el-dialog>
    <el-main>
      <!--  -->
      <div class="head">
        <div>
          <el-input
            placeholder=""
            v-model="input3"
            class="input-with-select"
            :clearable="true"
            @clear="orders"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="orders"
            ></el-button>
          </el-input>
        </div>
      </div>
      <el-table
        :data="tableData"
        stripe
        border
        row-key="cat_id"
        style="width: 100%"
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
      >
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column label="" prop="order_number">
        </el-table-column>
        <el-table-column label="" prop="order_price"> </el-table-column>
        <el-table-column label="">
          <template slot-scope="props">
            <el-tag v-show="props.row.pay_status == 1" effect="dark"
              ></el-tag
            >
            <el-tag
              v-show="props.row.pay_status != 1"
              effect="dark"
              type="danger"
              ></el-tag
            >
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"> </el-table-column>
        <el-table-column label="">
          <template slot-scope="props">
            {{ props.row.create_time | lwh_fro("yyyy-M-d h:m:s") }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="props">
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top-start"
            >
              <el-button size="mini" type="primary" @click="xg">
                <i class="el-icon-edit"></i>
              </el-button>
            </el-tooltip>
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top-start"
            >
              <el-button size="mini" type="success" @click="dz1(props.row)">
                <i class="el-icon-location"></i>
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div class="foot">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="100"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-main>
  </div>
</template>

<script>
// import axios from "axios";
import oaa from "../../public/city_data2017_element";
export default {
  name: "",
  // 
  components: {},
  // 
  props: {},
  // 
  data() {
    return {
      tableData: [],
      input3: "",
      pagenum: 1,
      pagesize: 5,
      total: 0,
      currentPage4: 1,
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      cityOptions: oaa,
      activities: [],
      ruleForm1: {
        name: "",
        fj: "",
      },
      rules1: {
        name: [{ required: true, message: "", trigger: "blur" }],
        fj: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  // Vue 
  created() {},
  // Vue  DOM
  mounted() {
    this.orders();
    console.log(oaa);
  },
  // vue
  update() {},
  // 
  methods: {
    handleSizeChange(val) {
      this.pagesize = val;
      this.orders();
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.pagenum = val;
      this.orders();
      console.log(`: ${val}`);
    },
    // 
    async orders() {
      let { data } = await this.axios.get("orders", {
        params: {
          query: this.input3,
          pagenum: this.pagenum,
          pagesize: this.pagesize,
        },
      });
      console.log(data);
      this.tableData = data.data.goods;
      this.total = data.data.total;
    },
    async dz1() {
      let { data } = await this.axios.get("/kuaidi/1106975712662");
      data.data.map((i, index) => {
        if (index == data.data.length - 1) {
          i.icon = "el-timeline-item__icon el-icon-location";
          i.color = "rgb(11, 189, 135)";
        }
      });
      console.log(data.data);
      this.activities = data.data;
      this.dialogFormVisible1 = true;
    },
    // 
    xg() {
      // let data = await axios.get("/dz.json");
      // this.cityOptions = data.data;
      // console.log(data.data);

      this.dialogFormVisible2 = true;
    },
    xg2() {
      this.$message({
        message: "",
        type: "success",
      });
      this.dialogFormVisible2 = false;

    },
    // 
  },
  // 
  filters: {
    lwh_fro(val, cont) {
      function dateFormat(date, format) {
        if (typeof date === "string") {
          var mts = date.match(/(\/Date\((\d+)\)\/)/);
          if (mts && mts.length >= 3) {
            date = parseInt(mts[2]);
          }
        }
        date = new Date(date);
        if (!date || date.toUTCString() == "Invalid Date") {
          return "";
        }
        var map = {
          M: date.getMonth() + 1, //
          d: date.getDate(), //
          h: date.getHours(), //
          m: date.getMinutes(), //
          s: date.getSeconds(), //
          q: Math.floor((date.getMonth() + 3) / 3), //
          S: date.getMilliseconds(), //
        };
        format = format.replace(/([yMdhmsqS])+/g, function (all, t) {
          var v = map[t];
          if (v !== undefined) {
            if (all.length > 1) {
              v = "0" + v;
              v = v.substr(v.length - 2);
            }
            return v;
          } else if (t === "y") {
            return (date.getFullYear() + "").substr(4 - all.length);
          }
          return all;
        });
        return format;
      }
      return dateFormat(val, cont);
    },
  },
  // 
  computed: {},
  // 
  watch: {},
};
</script>

<style lang="scss" scoped>
.dd {
  margin-top: 20px;
}
.el-main {
  background: #fff;
  border-radius: 10px;
  text-align: left;
}
.head {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.el-table {
  font-size: 12px;
}
.foot {
  margin-top: 20px;
}
.el-cascader {
  width: 100%;
}
</style>
